const toastRootId = "ui-toast-root";

export function initToastRoot() {
  let root = document.getElementById(toastRootId);
  if (!root) {
    root = document.createElement("div");
    root.id = toastRootId;
    root.className = "ui-toast";
    document.body.appendChild(root);
  }
  return root;
}

export function showToast(message, variant = "info", timeout = 2400) {
  const root = initToastRoot();
  const item = document.createElement("div");
  const variantClass = variant === "success"
    ? "ui-toast__item--success"
    : variant === "warning"
      ? "ui-toast__item--warning"
      : variant === "danger"
        ? "ui-toast__item--danger"
        : "";

  item.className = `ui-toast__item ${variantClass}`.trim();
  item.textContent = message;
  root.appendChild(item);

  setTimeout(() => {
    item.style.opacity = "0";
    item.style.transform = "translateY(-6px)";
    setTimeout(() => item.remove(), 200);
  }, timeout);
}

export function openModal(modalId, { title, message } = {}) {
  const modal = document.getElementById(modalId);
  if (!modal) return;

  if (title) {
    const titleEl = modal.querySelector(".ui-modal__title");
    if (titleEl) titleEl.textContent = title;
  }
  if (message) {
    const bodyEl = modal.querySelector(".ui-modal__body");
    if (bodyEl) bodyEl.textContent = message;
  }

  modal.classList.add("ui-modal--open");
}

export function closeModal(modalId) {
  const modal = document.getElementById(modalId);
  if (!modal) return;
  modal.classList.remove("ui-modal--open");
}

export function bindModalClose(modalId) {
  const modal = document.getElementById(modalId);
  if (!modal) return;

  modal.addEventListener("click", (event) => {
    if (event.target === modal || event.target?.dataset?.modalClose === "true") {
      closeModal(modalId);
    }
  });
}

export function setBadge(el, label, variant = "default") {
  if (!el) return;
  el.textContent = label;
  el.className = `ui-badge ${variantClass("ui-badge", variant)}`.trim();
}

function variantClass(base, variant) {
  if (variant === "success") return `${base}--success`;
  if (variant === "warning") return `${base}--warning`;
  if (variant === "danger") return `${base}--danger`;
  return "";
}
