<template>
  <section class="page" data-page="login">
    <div class="ui-card login-panel">
      <div class="stack-sm">
        <div class="page-title">환영합니다</div>
        <p class="page-subtitle">재고 관리에 로그인하세요</p>
      </div>
      <div class="ui-divider"></div>
      <div id="login-form" class="stack" >
        <div>
          <label class="ui-label">아이디</label>
          <input class="ui-input" type="text" required  v-model="state.form.loginId" />
        </div>
        <div>
          <label class="ui-label">비밀번호</label>
          <input class="ui-input" type="password" placeholder="********" v-model="state.form.password" required />
        </div>
        <button class="ui-btn" @click="submit()">로그인</button>
      </div>
      <div class="ui-alert ui-alert--info" style="margin-top: 14px;">
        계정 정보는 데모용으로 저장되지 않습니다.
      </div>
    </div>
  </section>

</template>

<script setup>
import {reactive} from "vue";
import axios from "axios";
import store from "@/assets/js/store";
import router from "@/assets/js/router";

const state = reactive({
  form :{
    loginId:"",
    password:""
  }
})

const submit = ()=>{
  axios.post("/api/account/login", state.form).then((res)=>{
    store.commit('setAccount', res.data);
    router.push({path:"/dashboard"});

  }).catch(()=>{
    window.alert("로그인 정보가 존재하지 않습니다.");
  })
}


</script>

