<template>
<body>
    <div class="app">

        <Header v-if="!route.meta.hideHeader"/>
        <main class="app-main" >
            <div class="app-container">
             <RouterView/>
            </div>
        </main>
    </div>

    <div id="stock-modal" class="ui-modal">
        <div class="ui-modal__content">
            <div class="ui-modal__title">재고 부족</div>
            <p class="ui-modal__body text-muted">출고 수량이 재고보다 많습니다.</p>
            <div class="ui-modal__actions">
                <button class="ui-btn ui-btn--ghost" data-modal-close="true" type="button">닫기</button>
            </div>
        </div>
    </div>
</body>
</template>

<script setup>
  import Header from '@/components/Header.vue'
  import store from "@/assets/js/store";
  import {watch} from "vue";
  import {useRoute} from "vue-router";
  import axios from "axios";
  import router from "@/assets/js/router";

  const check = ()=>{
    axios.get("/api/account/check").then(({data})=>{
      console.log(data);
      store.commit("setAccount", data ||0);

    }).catch((error)=>
    {
      if (error.response && error.response.status === 401)  router.push('/login');
    })
  }

  const route = useRoute();
  // router 가 바뀔때 마다 실행
  watch(route,()=>{
    check();
  })

  const id =  sessionStorage.getItem("id");

  if(id){
    store.commit("setAccount", id);
  }

</script>

<style>
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
</style>
