<template>
  <header class="app-header">
    <div class="app-logo">Mini Inventory</div>
    <button class="nav-toggle" id="nav-toggle" type="button" aria-expanded="false"  @click="toggleMenu"  aria-controls="main-nav">
      메뉴
    </button>
    <nav class="nav" :class="{ 'nav--open': isOpen }"  id="main-nav">
<!--      <router-link to="/login" class="nav-link" v-if="!store.state.account.id">로그인</router-link>-->
<!--      <a @click="logout" class="nav-link" v-else>로그아웃</a>-->
      <router-link to="/dashboard" class="nav-link"  @click="isOpen = false" >대시보드</router-link>
      <router-link to="/ItemAdd" class="nav-link"  @click="isOpen = false">제품 등록</router-link>
      <router-link to="/itemList" class="nav-link"  @click="isOpen = false">재고 조회</router-link>
      <router-link class="nav-link" to="/StockManagement" @click="isOpen = false"  >입고/출고 등록</router-link>
      <router-link class="nav-link" to="/StockHistory"  @click="isOpen = false" >재고 이력</router-link>
    </nav>
    <div class="app-header__right">
<!--      <span class="ui-badge ui-badge&#45;&#45;success">온라인</span>-->
      <button class="ui-btn ui-btn--ghost" @click="logout()">로그아웃</button>
    </div>
  </header>
</template>
<script setup>
import { useStore } from 'vuex'
import router from "@/assets/js/router";
import { ref } from 'vue'

const store = useStore()
const isOpen = ref(false)

const logout = ()=>{

  store.commit('setAccount', 0);
  sessionStorage.removeItem('id');
  router.push({path:'/login'});

}
function toggleMenu() {
  isOpen.value = !isOpen.value
}
</script>
