<template>
  <section class="page" data-page="register">
    <div class="page-header">
      <div>
        <div class="page-title">제품 등록</div>
        <div class="page-subtitle">새 제품을 등록합니다</div>
      </div>
      <button id="register-go-inventory" class="ui-btn ui-btn--ghost" type="button">재고 조회로 이동</button>
    </div>

    <div class="ui-card">
      <div id="register-form" class="stack">
        <div class="form-grid">
          <div>
            <label class="ui-label">제품 ID</label>
            <input class="ui-input" name="productId" type="text" placeholder="P-1004" v-model="state.form.itemCd" required />
          </div>
          <div>
            <label class="ui-label">제품명</label>
            <input class="ui-input" name="productName" type="text" placeholder="USB-C 허브" v-model="state.form.name" required />
          </div>
<!--          <div>-->
<!--            <label class="ui-label">카테고리</label>-->
<!--            <input class="ui-input" name="category" type="text" placeholder="액세서리" required />-->
<!--          </div>-->
          <div>
            <label class="ui-label">가격</label>
            <input class="ui-input" name="price" type="number" placeholder="45000" v-model="state.form.price" required />
          </div>
          <div>
            <label class="ui-label">초기 재고</label>
            <input class="ui-input" name="stock" type="number" placeholder="20" v-model="state.form.stock" required />
          </div>
        </div>
        <div class="ui-alert ui-alert--info">
          등록 후 재고 조회에 즉시 반영됩니다.
        </div>
        <button class="ui-btn ui-btn--success" @click="save()">제품 등록</button>
      </div>
    </div>
  </section>

</template>

<script setup>
import {reactive} from "vue";
import axios from "axios";
import router from "@/assets/js/router";

const state = reactive({
  form :{
    itemCd:"",
    name:"",
    price:0,
    stock:0,
  }
})

const save = ()=>{
  axios.post("/api/item/save", state.form).then(()=>{
    router.push({path:"/itemList"});
    window.alert("제품 등록 성공했습니다.");
  }).catch((e)=>{
    window.alert(e);
  })
}
</script>

