<template>
  <section class="page" data-page="stock">
    <div class="page-header">
      <div>
        <div class="page-title">제품 입고/출고</div>
        <div class="page-subtitle">재고 수량을 조정합니다</div>
      </div>
    </div>

    <div class="ui-card">
      <div id="stock-form" class="stack">
        <div class="form-grid">
          <div>
            <label class="ui-label">제품 선택</label>
            <select class="ui-input" id="stock-product-select" name="stockProduct"  v-model="state.selectedItem"  required>
              <option
                  v-for="item in items"
                  :key="item.id"
                  :value="item"
              >
                {{ item.name }}
              </option>
            </select>
          </div>
          <div>
            <label class="ui-label">현재 재고</label>
            <div class="ui-input">{{state.selectedItem.stock}}</div>
          </div>
          <div>
            <label class="ui-label">입고/출고</label>
            <select class="ui-input" name="stockType"  v-model="state.form.type"  required>
              <option value="in">입고</option>
              <option value="out">출고</option>
            </select>
          </div>
          <div>
            <label class="ui-label">수량</label>
            <input class="ui-input" name="quantity" type="number" placeholder="5"   v-model="state.form.qty" required />
          </div>
        </div>
        <div class="ui-alert ui-alert--warning">
          출고는 재고 수량을 확인한 뒤 처리하세요.
        </div>
        <button class="ui-btn ui-btn--success" @click="save()">재고 처리</button>
      </div>
    </div>
  </section>
</template>

<script setup>
import {ref, onMounted, reactive} from 'vue'
import axios from 'axios'
import router from "@/assets/js/router";
import store from "@/assets/js/store";
//import utility from "@/assets/js/utility";
const items = ref([])
onMounted(() => {
  fetchItems()
})

const fetchItems = async()=>{
  try{
    const response = await axios.get('/api/items')

    items.value = response.data;
  }catch (error)
  {
    window.alert('데이터 조회 실패:', error)
  }
}

const state = reactive({
 form:{
   id: null,
   itemId: 0,
   type: '',
   qty: 0,
   createUser:'',
   createDate: null
 },
 selectedItem : {
   id:0
 }

})

const save = async()=>{

    state.form.createUser = store.state.account.id.toString();
    const now = new Date();
    state.form.createDate =now;
    state.form.itemId = state.selectedItem.id;

    console.log('요청 파라미터 :',state.form);
    await axios.post('/api/stock/save', state.form).then(()=>{
      window.alert("재고 등록 성공했습니다.");
      router.push({path:"/itemList"});
    }).catch((e)=>{
      window.alert(e);
    })
}
</script>


