<template>
  <section class="page" data-page="dashboard">
    <div class="page-header">
      <div>
        <div class="page-title">제품 현황 대시보드</div>
        <div class="page-subtitle">실시간 재고 요약</div>
      </div>
      <button class="ui-btn ui-btn--ghost" id="inventory-refresh" type="button">현황 갱신</button>
    </div>

    <div class="app-grid grid-3">
      <div class="ui-card stats-card">
        <span class="stats-label">등록 제품 수</span>
        <span class="stats-value" id="dash-total-products">{{state.itemcount}}</span>
        <span class="ui-badge">전체</span>
      </div>
      <div class="ui-card stats-card">
        <span class="stats-label">총 재고 수량</span>
        <div class="stats-value" id="dash-total-stock">{{state.totalstockqty}}</div>
        <span class="ui-badge ui-badge--success">합계</span>
      </div>
      <div class="ui-card stats-card">
        <span class="stats-label">부족 재고 품목</span>
        <div class="stats-value" id="dash-low-stock">{{state.stockzeroitemqty}}</div>
        <span class="ui-badge ui-badge--danger">부족</span>
      </div>
    </div>

    <div class="ui-card" style="margin-top: 20px;">
      <div class="ui-card__title">최근 업데이트</div>
      <p class="text-muted">최신 재고 반영 시간</p>
      <div class="ui-divider"></div>
      <div class="ui-alert ui-alert--info">
        마지막 반영 시각: <strong id="dash-latest-update">- {{utility.formatDateTime(state.lastdata.createDate) }}</strong>
      </div>
    </div>
  </section>


</template>

<script setup>
import {reactive} from "vue";
import axios from "axios";
import utility from "../assets/js/utility";

const state = reactive({
  itemcount:0,
  totalstockqty:0,
  stockzeroitemqty:0,
  lastdata:{},
})

axios.get('/api/items/monitor').then((res)=>{
  state.itemcount = res.data[0];
  state.totalstockqty = res.data[1];
  state.stockzeroitemqty = res.data[2];
});

axios.get('/api/stock/getLastStockHisotry').then((res)=>{
  console.log('마지막 데이터', res.data);
  state.lastdata = res.data.data;
});
</script>

