<template>
  <section class="page" data-page="history">
    <div class="page-header">
      <div>
        <div class="page-title">제품 재고 이력</div>
        <div class="page-subtitle">최근 재고 이동 내역</div>
      </div>
      <div class="ui-pill-group">
        <select class="ui-input history-filter" id="history-filter" aria-label="제품별 이력 필터"  v-model="selecteditem">
          <option
              v-for="item in items"
              :key="item.id"
              :value="item"
          >
            {{ item.name }}
          </option>
        </select>
        <div class="ui-badge">최근 5건</div>
      </div>
    </div>

    <div class="ui-card">
      <div id="history-list" class="history-list">
        <div class="history-item" v-for="(data, idx) in dataList" :key="idx">
          <div class="history-item__meta">
            <strong>{{data.item.name}}</strong>
            <span class="text-muted">{{utility.formatDateTime(data.createDate)}}</span>
          </div>
          <div class="ui-pill-group">
            <span class="ui-badge" :class="data.type === 'in' ?  'ui-badge--success' : 'ui-badge--warning'">{{data.type ==='in' ? '입고' : '출고'}}</span>
            <span class="ui-chip">{{data.qty}} 개</span>
          </div>
        </div>
      </div>
    </div>
  </section>
</template>

<script setup>
import {ref, onMounted, reactive, watch} from 'vue';
import axios from 'axios';
import utility from "../assets/js/utility";


// 컴포넌트가 마운트(화면 표시)된 직후 호출
onMounted(() => {

  getItems();
  fetchData(null);
});

const dataList = ref([]);
const items = ref([]);
const selecteditem = ref({ id: 9999, name: '전체 선택' });
const state = reactive({
  items:[]
})
const getItems = async () =>{
  // 전체 제품 조회
  await axios.get('/api/items').then((res)=>{
    items.value = res.data // 복사됨
    items.value.unshift({ id: 9999, name: '전체 선택' });
    state.items = res.data
    //state.items.unshift({ id: 9999, name: '전체 선택' });
  });
}


const fetchData = async (newValue) => {
  try {

    if(newValue == null || newValue.id === 9999)
    {
      const response = await axios.get('/api/stock/stockHistoryList');
      console.log('응답 객체', response.data.data);
      // 데이터 할당
      dataList.value = response.data.data;
    }else{
      const response = await axios.get('/api/stock/stockHistoryListByItemId', {
        params: {
          itemId: newValue.id  // 백엔드에서 받는 파라미터 이름 (예: @RequestParam Integer itemId)
        }
      });
      dataList.value = response.data.data;


    }


  } catch (error) {
    window.alert(error);
  }
};

// selecteditem 값이 바뀌면 자동으로 fetchData 실행
watch(selecteditem, (newValue) => {

  fetchData(newValue);
});

</script>


<style scoped>

</style>