import { showToast, openModal, closeModal, bindModalClose } from "./ui.js";

const state = {
    products: [
        { id: "P-1001", name: "무선 키보드", category: "주변기기", price: 39000, stock: 42 },
        { id: "P-1002", name: "노이즈 캔슬링 헤드셋", category: "오디오", price: 129000, stock: 18 },
        { id: "P-1003", name: "27인치 모니터", category: "디스플레이", price: 219000, stock: 12 }
    ],
    history: [
        { id: "H-501", product: "무선 키보드", type: "입고", qty: 20, at: "2026-02-10 09:32" },
        { id: "H-502", product: "노이즈 캔슬링 헤드셋", type: "출고", qty: 5, at: "2026-02-11 15:20" },
        { id: "H-503", product: "27인치 모니터", type: "입고", qty: 8, at: "2026-02-13 10:05" }
    ]
};

const routes = [
    "login",
    "dashboard",
    "register",
    "inventory",
    "stock",
    "history"
];

const pageEls = [...document.querySelectorAll("[data-page]")];
const navLinks = [...document.querySelectorAll("[data-route]")];
const navToggle = document.querySelector("#nav-toggle");
const mainNav = document.querySelector("#main-nav");
let currentHistoryFilter = "all";

function setActivePage(route) {
    const target = routes.includes(route) ? route : "dashboard";
    pageEls.forEach((page) => {
        const isActive = page.dataset.page === target;
        page.classList.toggle("page--active", isActive);
    });

    navLinks.forEach((link) => {
        link.classList.toggle("nav-link--active", link.dataset.route === target);
    });

    if (target === "inventory") renderInventory();
    if (target === "history") {
        hydrateHistoryFilter();
        renderHistory();
    }
    if (target === "dashboard") renderDashboard();
}

function renderInventory() {
    const body = document.querySelector("#inventory-table-body");
    if (!body) return;
    body.innerHTML = "";
    state.products.forEach((item) => {
        const row = document.createElement("tr");
        row.innerHTML = `
      <td>${item.id}</td>
      <td>${item.name}</td>
      <td>${item.category}</td>
      <td>₩${item.price.toLocaleString()}</td>
      <td>${item.stock}</td>
      <td><span class="ui-badge ${item.stock < 10 ? "ui-badge--danger" : item.stock < 20 ? "ui-badge--warning" : "ui-badge--success"}">
        ${item.stock < 10 ? "부족" : item.stock < 20 ? "주의" : "안정"}
      </span></td>
    `;
        body.appendChild(row);
    });
}

function renderHistory() {
    const list = document.querySelector("#history-list");
    if (!list) return;
    list.innerHTML = "";
    const filtered = state.history
        .filter((item) => currentHistoryFilter === "all" || item.product === currentHistoryFilter)
        .slice()
        .reverse();

    if (filtered.length === 0) {
        const empty = document.createElement("div");
        empty.className = "ui-alert ui-alert--info";
        empty.textContent = "선택한 제품의 이력이 없습니다.";
        list.appendChild(empty);
        return;
    }

    filtered.forEach((item) => {
        const node = document.createElement("div");
        const badgeClass = item.type === "입고" ? "ui-badge--success" : "ui-badge--warning";
        node.className = "history-item";
        node.innerHTML = `
      <div class="history-item__meta">
        <strong>${item.product}</strong>
        <span class="text-muted">${item.at}</span>
      </div>
      <div class="ui-pill-group">
        <span class="ui-badge ${badgeClass}">${item.type}</span>
        <span class="ui-chip">${item.qty}개</span>
      </div>
    `;
        list.appendChild(node);
    });
}

function hydrateHistoryFilter() {
    const select = document.querySelector("#history-filter");
    if (!select) return;

    const names = Array.from(new Set(state.products.map((item) => item.name)));
    const previousValue = currentHistoryFilter;
    select.innerHTML = "";

    const allOption = document.createElement("option");
    allOption.value = "all";
    allOption.textContent = "전체 제품";
    select.appendChild(allOption);

    names.forEach((name) => {
        const option = document.createElement("option");
        option.value = name;
        option.textContent = name;
        select.appendChild(option);
    });

    if (previousValue && [...select.options].some((opt) => opt.value === previousValue)) {
        select.value = previousValue;
        currentHistoryFilter = previousValue;
    } else {
        currentHistoryFilter = "all";
        select.value = "all";
    }
}

function renderDashboard() {
    const totalProducts = document.querySelector("#dash-total-products");
    const totalStock = document.querySelector("#dash-total-stock");
    const lowStock = document.querySelector("#dash-low-stock");
    const latestUpdate = document.querySelector("#dash-latest-update");

    const stockSum = state.products.reduce((sum, item) => sum + item.stock, 0);
    const lowCount = state.products.filter((item) => item.stock < 10).length;
    totalProducts.textContent = state.products.length.toString();
    totalStock.textContent = stockSum.toString();
    lowStock.textContent = lowCount.toString();
    if (state.history[0]) latestUpdate.textContent = state.history[state.history.length - 1].at;
}

function onRegisterSubmit(event) {
    event.preventDefault();
    const form = event.target;
    const formData = new FormData(form);
    const id = formData.get("productId").toString().trim();
    const name = formData.get("productName").toString().trim();
    const category = formData.get("category").toString().trim();
    const price = Number(formData.get("price"));
    const stock = Number(formData.get("stock"));

    if (!id || !name || !category || Number.isNaN(price) || Number.isNaN(stock)) {
        showToast("모든 필드를 입력해 주세요.", "warning");
        return;
    }

    state.products.push({ id, name, category, price, stock });
    form.reset();
    showToast("제품이 등록되었습니다.", "success");
    hydrateProductSelect();
    hydrateHistoryFilter();
    renderDashboard();
    setActivePage("inventory");
}

function onStockSubmit(event) {
    event.preventDefault();
    const form = event.target;
    const formData = new FormData(form);
    const productId = formData.get("stockProduct").toString();
    const qty = Number(formData.get("quantity"));
    const type = formData.get("stockType").toString();

    const product = state.products.find((item) => item.id === productId);
    if (!product || Number.isNaN(qty) || qty <= 0) {
        showToast("정확한 정보를 입력해 주세요.", "warning");
        return;
    }

    if (type === "출고" && product.stock < qty) {
        openModal("stock-modal", {
            title: "재고 부족",
            message: "출고 수량이 재고보다 많습니다."
        });
        return;
    }

    product.stock += type === "입고" ? qty : -qty;
    state.history.push({
        id: `H-${500 + state.history.length + 1}`,
        product: product.name,
        type,
        qty,
        at: new Date().toISOString().replace("T", " ").slice(0, 16)
    });

    showToast(`제품 ${type} 처리 완료`, "success");
    form.reset();
    renderInventory();
    hydrateHistoryFilter();
    renderHistory();
    renderDashboard();
}

function onLogin(event) {
    event.preventDefault();
    showToast("환영합니다!", "success");
    setActivePage("dashboard");
}

function bindNav() {
    navLinks.forEach((link) => {
        link.addEventListener("click", (event) => {
            event.preventDefault();
            setActivePage(link.dataset.route);
            if (mainNav) mainNav.classList.remove("nav--open");
            if (navToggle) navToggle.setAttribute("aria-expanded", "false");
        });
    });

    navToggle?.addEventListener("click", () => {
        window.alert('test');
        if (!mainNav) return;
        mainNav.classList.toggle("nav--open");
        const isOpen = mainNav.classList.contains("nav--open");
        navToggle.setAttribute("aria-expanded", isOpen ? "true" : "false");
    });
}

function hydrateProductSelect() {
    const select = document.querySelector("#stock-product-select");
    if (!select) return;
    select.innerHTML = "";
    state.products.forEach((item) => {
        const option = document.createElement("option");
        option.value = item.id;
        option.textContent = `${item.name} (${item.id})`;
        select.appendChild(option);
    });
}

function initForms() {
    const registerForm = document.querySelector("#register-form");
    const stockForm = document.querySelector("#stock-form");
    const loginForm = document.querySelector("#login-form");
    const historyFilter = document.querySelector("#history-filter");

    registerForm?.addEventListener("submit", onRegisterSubmit);
    stockForm?.addEventListener("submit", onStockSubmit);
    loginForm?.addEventListener("submit", onLogin);
    historyFilter?.addEventListener("change", (event) => {
        currentHistoryFilter = event.target.value;
        renderHistory();
    });
}

function init() {
    bindNav();
    hydrateProductSelect();
    hydrateHistoryFilter();
    initForms();
    bindModalClose("stock-modal");
    setActivePage("login");
}

document.addEventListener("DOMContentLoaded", () => {
    init();
});

window.addEventListener("inventory:update", () => {
    hydrateProductSelect();
    hydrateHistoryFilter();
    renderInventory();
    renderDashboard();
});

const inventoryButton = document.querySelector("#inventory-refresh");
if (inventoryButton) {
    inventoryButton.addEventListener("click", () => {
        renderInventory();
        showToast("재고 데이터가 갱신되었습니다.", "info");
    });
}

const registerButton = document.querySelector("#register-go-inventory");
if (registerButton) {
    registerButton.addEventListener("click", () => {
        setActivePage("inventory");
    });
}

const stockModalClose = document.querySelector("[data-modal-close='true']");
if (stockModalClose) {
    stockModalClose.addEventListener("click", () => closeModal("stock-modal"));
}